import React from "react";
import { Typography } from "antd";
const { Title, Paragraph, Text, Link } = Typography;

function VirtualRun() {
  return (
    <div>
      <Title>Virtual Run</Title>

      <iframe
        width="560"
        height="315"
        src="https://www.youtube.com/embed/59bK7v4w-RM"
        title="YouTube video player"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
        allowFullScreen
      ></iframe>

      <Paragraph>
        Virtual Run is an Augmented Reality application developed as the
        capstone project for the class EECS 498: Mixed Reality. Our product
        allows runners of all skill levels to record themselves walking, jogging
        and running to create a "virtual" ghost that they can playback and run
        with using augmented reality.
      </Paragraph>

      <Paragraph>
        The application was built using Unity 3D over a period of 4 weeks for
        Android and iOS devices. For the map components of our game, we used the
        Mapbox Unity package. Players' running data is recorded from the user's
        GPS. Players also have the option of saving their run recordings to our
        backend database, where they can be replayed later or shared with
        friends. The visualization of the opponent runner and free run mode
        objects in the AR camera view were done using Unity's built in
        ARFoundation modules.
      </Paragraph>

      <Link href="https://kywalker2.wixsite.com/virtual-run" target="_blank">
        Check out our product website here!
      </Link>
    </div>
  );
}

export default VirtualRun;
