import React from "react";
import { Typography } from "antd";
const { Title, Paragraph, Text, Link } = Typography;

function EmbeddedDesign() {
  return (
    <div>
      <Title>Embedded Systems Design: Piano Hands</Title>

      <iframe
        width="560"
        height="315"
        src="https://www.youtube.com/embed/gMaK7hlrJtk"
        title="YouTube video player"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
        allowFullScreen
      ></iframe>

      <Paragraph>
        Piano Hands was my team's final project submission for the class EECS
        373: Intro to Embedded Systems Design at the University of Michigan. Our
        team consisted of Kevin Choong, James Terng, Mandy He and myself and was
        completed over the course of 1 month. Piano Hands was envisioned as a
        wearable electronic instrument that would give musicians access to the
        full range of notes from a piano keyboard without the need of a physical
        instrument.
      </Paragraph>

      <Paragraph>
        Our device uses a STM32 Nucleo development board as the main computing
        unit. A robust sensor package captures the natural playing motions of a
        pianist. Force sensitive resistors capture the striking force of the
        user's fingers and convert them into MIDI signals that are transported
        via UART to a synthesizer module for audio generation. These MIDI
        signals can also be sent to a personal computer via a Xbee RF
        communications unit on the glove.
      </Paragraph>

      <Paragraph>
        To capture the horizontal motion of the user's hands, our team uses a
        PIXY camera with vision capabilities. This system measures the distance
        of the glove to a reference object and subsequently transposes the range
        of notes mapped to each finger up and down. With this, the user is able
        to access the full range of 88-keys of a traditional piano keyboard.
      </Paragraph>

      <Paragraph>
        The code for this project can be found on my{" "}
        <Link href="https://github.com/jastinjn/Piano-Hands" target="_blank">
          Github
        </Link>{" "}
        page.
      </Paragraph>
    </div>
  );
}

export default EmbeddedDesign;
