import React from "react";
import { Typography } from "antd";
import { Image } from "antd";
const { Title, Paragraph, Text, Link } = Typography;

function ComputerVision() {
  return (
    <div>
      <Title>Audio-Visual Localization: Objects that Sound</Title>
      <Paragraph>
        For our final project submission for the class EECS 442: Computer Vision
        at the University of Michigan, my parter {" (Joel Huang) "} and I
        reimplemented the Audio-Visual Object Localization {" (AVOL) "}network
        originally published in the paper "Objects that Sound" by Relja
        Arandjelovi and Andrew Zisserman. The main task of our network is to
        identify the location of the sound source in a video. For example, in a
        video of a man playing a guitar, the model should be able to identify
        the part of the image where the mans hand touches the guitar as the
        sound source.
      </Paragraph>
      <Image src="images/arch.png" width="50%" />
      <Paragraph>
        AVOlnet uses a deep convolutional neural network to generate feature
        embeddings of both the video and audio spectrogram data from the frames
        of an input video source. The scalar product of these embeddings are
        then used to predict the approximate location of the sound source
        present in the video. Our implementation was written us with the PyTorch
        library and trained over a period of 20 hours on a GTX 1060 Max-Q using
        the AudioSet-Instruments dataset.
      </Paragraph>
      <Paragraph>
        Our full implementation managed to achieve an accuracy of 70.9% on the
        validation set for the audio-visual correspondence task, meaning it was
        able to predict if the image and spectrogram were from the same video
        clip approximately 70% of the time. We found that our model was able to
        generate fairly accurate predictions for the locations of sounding
        objects. Some examples can be seen below.
      </Paragraph>
      <Image src="images/objectlocalisation.png" width="80%" />
      <Paragraph>
        <Link
          href="https://github.com/jastinjn/ObjectsThatSound"
          target="_blank"
        >
          Project Code
        </Link>{" "}
        and{" "}
        <Link href="documents/442FinalReport.pdf" download target="_blank">
          Final Report
        </Link>
      </Paragraph>
    </div>
  );
}

export default ComputerVision;
