import React from "react";
import { Typography } from "antd";
const { Title, Paragraph, Text, Link } = Typography;

function AugmentedReality() {
  return (
    <div>
      <Title>Ann Arbor Go</Title>

      <iframe
        width="560"
        height="315"
        src="https://www.youtube.com/embed/Yz-DCoy32Pk"
        title="YouTube video player"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
        allowFullScreen
      ></iframe>

      <Paragraph>
        For EECS 498: Mixed Reality Project 2, our team created a mobile game
        inspired by the hit AR application "Pokemon Go". In our game, players
        are able to traverse the city of Ann Arbor and plant a variety of trees.
        After these trees are fully grown, the player earns revenue which can be
        used to purchase new types of seeds and items in an in-game shop. These
        trees can be attacked by Ann Arbor's signature fat squirrels, so the
        player is armed with an acorn shooter to suppress any rodent threats.
      </Paragraph>

      <Paragraph>
        For our project's custom feature, we added the ability to summon a
        terrible meteor to destroy all of the player's planted trees in a
        devastating cataclysm, effectively resetting the game.
      </Paragraph>

      <Paragraph>
        The application was built using Unity over a period of 3 weeks and
        tested on an Android mobile device. For the map components of our game,
        we used the Mapbox Unity package which gives access to the user's local
        position and map data. The world interaction is achieved using Unity's
        built-in AR features.
      </Paragraph>
    </div>
  );
}

export default AugmentedReality;
